<template>
    <div class="table">
        <div class="container">
            <div class="handle-box">
                <el-form :inline="true" label-width="80px">
                    <el-form-item label="订单状态">
                        <el-select
                            v-model="parames.orderStatus"
                            placeholder="订单状态"
                            @change="getDataList"
                            class="handle-select"
                        >
                            <el-option key="-1" label="全部" :value="-1"></el-option>
                            <el-option key="0" label="未支付" :value="0"></el-option>
                            <el-option key="1" label="已支付" :value="1"></el-option>
                            <el-option key="2" label="已发货" :value="2"></el-option>
                            <el-option key="3" label="订单关闭" :value="4"></el-option>
                            <el-option key="4" label="已完成" :value="5"></el-option>
                            <el-option key="5" label="退款中" :value="6"></el-option>
                            <el-option key="6" label="已退款" :value="7"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="订单号">
                        <el-input
                            v-model="parames.orderId"
                            placeholder="请输入订单号"
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="联系电话">
                        <el-input
                            v-model="parames.userMobile"
                            placeholder="请输入联系电话"
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="商品标题">
                        <el-input
                            v-model="parames.goodsName"
                            placeholder="请输入商品标题"
                            class="handle-input"
                        ></el-input>
                    </el-form-item>
                    <el-form-item label="交易时间">
                        <el-select
                            v-model="parames.timeType"
                            placeholder="请选择交易时间"
                            class="handle-select"
                        >
                            <el-option key="-1" label="全部" :value="-1"></el-option>
                            <el-option key="0" label="下单时间" :value="1"></el-option>
                            <el-option key="1" label="付款时间" :value="2"></el-option>
                            <el-option key="2" label="发货时间" :value="3"></el-option>
                            <el-option key="3" label="收货时间" :value="4"></el-option>
                            <el-option key="4" label="退款时间" :value="5"></el-option>
                        </el-select>
                        <el-date-picker
                            value-format="timestamp"
                            v-model="createDateList"
                            type="daterange"
                            start-placeholder="开始"
                            end-placeholder="结束"
                            @change="selectTime"
                        ></el-date-picker>
                    </el-form-item>
                    <el-button type="primary" @click="search">搜索</el-button>
                    <el-button  @click="exportExcel">导出Excel</el-button>

                </el-form>
            </div>
            <el-table :data="dataList" border class="table">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-table :data="props.row.orderGoodsVoList" border class="table">
                            <el-table-column label="订单信息" min-width="100">
                                <template slot-scope="scope">
                                    <div class="goods-info">
                                        <div class="goods-img-wrap">
                                            <img class="goods-img" :src="scope.row.image" alt>
                                        </div>
                                        <div class="goods-title">{{scope.row.goodsName}}</div>
                                    </div>
                                </template>
                            </el-table-column>
                            <el-table-column label="价格" min-width="60">
                                <template slot-scope="scope">
                                    <p>原价：{{scope.row.reservePrice}}元</p>
                                    <p
                                        v-if="scope.row.price!=scope.row.reservePrice"
                                    >优惠价：{{scope.row.price}}元</p>
                                </template>
                            </el-table-column>
                            <el-table-column label="sku" min-width="60">
                                <template slot-scope="scope">
                                    <div v-if="scope.row.skuPropertyList.length>0">
                                        <p
                                            v-for="item in scope.row.skuPropertyList"
                                            :key="item.goodsId"
                                        >{{item.propertyName}}:{{item.valueAlias}}</p>
                                    </div>
                                    <div v-else>无</div>
                                </template>
                            </el-table-column>
                            <el-table-column label="数量" prop="goodsNum" min-width="50"></el-table-column>
                        </el-table>
                    </template>
                </el-table-column>
                <el-table-column label="订单号" prop="orderId" min-width="70"></el-table-column>
                <el-table-column label="总价" prop="totalPrice" min-width="35"></el-table-column>
                <el-table-column label="总数量" prop="goodsNum" min-width="35"></el-table-column>
                <el-table-column label="收货人信息" min-width="60">
                    <template slot-scope="scope">
                        <p v-if="scope.row.acceptName">收货人：{{scope.row.acceptName}}</p>
                        <p v-if="scope.row.acceptMobile">联系方式：{{scope.row.acceptMobile}}</p>
                        <p class="red" v-if="scope.row.remark">备注：{{scope.row.remark}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="交易时间" min-width="70">
                    <template slot-scope="scope">
                        <p v-if="scope.row.createTimeStr">创建{{scope.row.createTimeStr}}</p>
                        <p v-if="scope.row.paidTimeStr">付款{{scope.row.paidTimeStr}}</p>
                        <p v-if="scope.row.deliveryTimeStr">发货{{scope.row.deliveryTimeStr}}</p>
                        <p v-if="scope.row.acceptTimeStr">收货{{scope.row.acceptTimeStr}}</p>
                        <p v-if="scope.row.refundTimeStr">退款{{scope.row.refundTimeStr}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="订单状态" prop="orderStatusStr" min-width="40"></el-table-column>
                <el-table-column label="操作人" min-width="50">
                    <template slot-scope="scope">
                        <p
                            v-if="scope.row.deliveryOperateName"
                        >发货人:{{scope.row.deliveryOperateName}}</p>
                        <p
                            v-if="scope.row.refundGoodsOperateName"
                        >退货人:{{scope.row.refundGoodsOperateName}}</p>
                        <p v-if="scope.row.refundOperateName">退款人:{{scope.row.refundOperateName}}</p>
                        <p
                            v-if="scope.row.orderStatusRemark"
                            class="red"
                        >备注:{{scope.row.orderStatusRemark}}</p>
                    </template>
                </el-table-column>
                <el-table-column label="操作" min-width="80">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-lx-info"
                            class="grey"
                            @click="handleOrder('remark',scope.row)"
                        >备注</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-download"
                            @click="handleOrder('shipments',scope.row)"
                            v-if="scope.row.orderStatus==1"
                        >发货</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleOrder('shipments',scope.row)"
                            v-if="scope.row.orderStatus==2"
                        >编辑</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-lx-link"
                            @click="handleOrder('logistics',scope.row)"
                            v-if="scope.row.orderStatus==2"
                        >物流</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-lx-recharge"
                            class="red"
                            @click="handleOrder('pay',scope.row)"
                            v-if="scope.row.orderStatus==1 || scope.row.orderStatus==6"
                        >{{scope.row.orderStatus==1?'退款':'收货'}}</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-location-outline"
                            v-if="scope.row.orderStatus==1"
                            @click="handleOrder('location', scope.row)"
                        >地址</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            v-if="scope.row.status!=3"
                            @click="handleOrder('del', scope.row)"
                        >删除</el-button>
                        <el-button
                            type="text"
                            class="red"
                            icon="el-icon-download"
                            @click="handleOrder('refund',scope.row)"
                            v-if="scope.row.orderStatus==5"
                        >退货</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <my-page :pager="pager" @change="changePage"></my-page>
            </div>
        </div>
        <edit-location v-if="showLocationBox"></edit-location>
        <shipments v-if="showShipmentsBox"></shipments>
        <refund v-if="showRefundBox"></refund>
        <pay v-if="showPayBox"></pay>
        <logistics v-if="showLogisticsBox"></logistics>


    </div>
</template>

<script>
import EditLocation from "../../components/common/orderOperation/location/index.vue";
import Shipments from "../../components/common/orderOperation/shipments.vue";
import Refund from "../../components/common/orderOperation/refund.vue";
import Pay from "../../components/common/orderOperation/pay.vue";
import Logistics from "../../components/common/orderOperation/logistics.vue";



export default {
    name: "orderList",
    components: {
        EditLocation,
        Shipments,
        Refund,
        Pay,
        Logistics
    },
    data() {
        return {
            createDateList: [], //创建时间组
            pager: {
                total: 100,
                page: 1,
                rows: 20
            }, //分页参数

            dataList: [], //列表数据
            parames: {
                orderId: null,
                userMobile:null,
                goodsName:null,
                timeType: -1,
                startTime:-1,
                endTime:-1,
                orderStatus: -1, //订单状态：-1=全部 0=待付款，1=待发货，2=已发货，4=交易关闭，5=交易成功，20=退货未审核，30=退款未审核
                pageNo: 1,
                pageSize: 20
            }, //请求参数
            orderId: null,
            showLocationBox: false, //修改收货地址
            showShipmentsBox: false, //发货更新
            showRefundBox: false, //退货
            showPayBox:false,//退款 收货
            showLogisticsBox:false,//物流
            returnOrderNo:"",//可能为空
            expressId:"",//物流Id
        };
    },
    created() {
        this.getDataList();
    },

    activated() {
        if (this.$route.params.onload) {
            if (this.$route.params.onloadDeep) {
                this.parames = {
                    goodsName: "",
                    goodsStatus: "-1",
                    timeOnSaleSt: -1,
                    timeOnSaleEd: -1,
                    pageNo: 1,
                    pageSize: 20
                };
            }
            this.getDataList();
        }
    },
    methods: {
        async getDataList() {
            const data = await this.$api.OrderList(this.parames);

            if (data && data[0] && data[0].pageNum == 1) {
                this.pager.total = data[0].total;
                this.pager.rows = data[0].pageSize;
            } else if (this.parames.pageNo == 1 && data.length == 0) {
                this.pager.total = 0;
            }

            this.dataList = data;
        },
        //交易时间选择
        selectTime() {
            this.parames.startTime = this.createDateList
                ? this.createDateList[0]
                : -1;
            this.parames.endTime = this.createDateList
                ? this.createDateList[1]
                : -1;
        },
        //搜索
        search() {
            this.getDataList();
        },
        //导出
        exportExcel(){
            if(this.dataList.length>0){
                let str= `orderStatus=${this.parames.orderStatus}&goodsName=${this.parames.goodsName?this.parames.goodsName:''}&userMobile=${this.parames.userMobile?this.parames.userMobile:''}&orderId=${this.parames.orderId?this.parames.orderId:''}&orderType=${this.parames.orderType?this.parames.orderType:''}&timeType=${this.parames.timeType?this.parames.timeType:''}&endTime=${this.parames.endTime?this.parames.endTime:''}&startTime=${this.parames.startTime?this.parames.startTime:''}`
                window.open(`${process.env.VUE_APP_API_BASE}admin/order/export_order?${str}`)
            }else{
                this.$message.error('没有可导出的数据')
            }
        },
        //分页事件
        changePage() {
            this.parames.pageNo = this.pager.page == 0 ? 1 : this.pager.page;
            this.parames.pageSize = this.pager.rows;
            this.getDataList();
        },

        //订单处理
        handleOrder(status, row) {
            if (status === "remark") {
                this.$prompt("请输入备注信息", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    inputValue: row.remark
                })
                    .then(({ value }) => {
                        if (value === "") {
                            this.$message({
                                message: "备注不能为空",
                                type: "warning"
                            });
                        } else {
                            this.orderRemark(row.orderId, value);
                        }
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "取消输入"
                        });
                    });
            } else if (status === "del") {
                this.$confirm("删除该订单, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                    .then(() => {
                        this.goodsDel(row.goodsId);
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            } else if (status === "location") {
                this.orderId = row.orderId;
                this.showLocationBox = true;
            } else if (status === "shipments") {
                this.orderId = row.orderId;
                this.showShipmentsBox = true;
            } else if (status === "refund") {
                this.orderId = row.orderId;
                this.showRefundBox = true;
            } else if (status === "pay") {
                this.orderId = row.orderId;
                this.returnOrderNo=row.returnOrderNo
                this.showPayBox = true;
                
            } else if (status === "logistics") {
                this.orderId = row.orderId;
                this.expressId=row.wlOrder
                this.showLogisticsBox = true;
                
            }
        },
        //订单备注
        async orderRemark(orderId, remark) {
            const data = await this.$api.OrderRemark(
                { orderId, remark },
                orderId
            );
            this.$message({
                message: "备注成功",
                type: "success"
            });
            this.getDataList();
        },
        //订单删除
        async goodsDel(goodsId) {
            const data = await this.$api.GoodsDelete({ goodsId });
            this.$message({
                message: "删除成功",
                type: "success"
            });
            this.getDataList();
        }
    }
};
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 200px;
    display: inline-block;
}
.del-dialog-cnt {
    font-size: 16px;
    text-align: center;
}
.table {
    width: 100%;
    font-size: 14px;
}
.mr10 {
    margin-right: 10px;
}
.red {
    color: #ff0000;
}
.grey {
    color: #999;
}
</style>
