<template>
    <div class="shipments-wrap" @click.self="$parent.showShipmentsBox=false">
        <div class="shipments">
            <div class="shipments-header">
                {{title}}发货信息
                <i
                    class="el-icon-circle-close-outline"
                    style="font-size:26px;color:#888"
                    @click="$parent.showShipmentsBox=false"
                ></i>
            </div>
            <el-form label-width="80px" style="padding:10px 30px 0">
                <el-form-item label="物流公司">
                    <el-select v-model="parames.expressName" placeholder="请选择物流公司">
                        <el-option
                            v-for="item in options"
                            :key="item.label"
                            :label="item.label"
                            :value="item.label"
                        ></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="物流单号">
                    <el-input v-model="parames.expressId" placeholder="请输入物流单号"></el-input>
                </el-form-item>
                <el-form-item label="订单备注">
                    <el-input v-model="parames.remark"></el-input>
                </el-form-item>
                <div style="display: flex;justify-content: center;">
                    <el-button size="mini" @click="$parent.showShipmentsBox=false">取消</el-button>
                    <el-button type="primary" size="mini" @click="updateShipments">保存</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    name: "shipments",
    data() {
        return {
            options: [
                {
                    value: "顺丰速运",
                    label: "顺丰速运"
                },
                {
                    value: "中国邮政",
                    label: "中国邮政"
                },
                {
                    value: "圆通速递",
                    label: "圆通速递"
                },
                {
                    value: "中通快递",
                    label: "中通快递"
                },
                {
                    value: "申通快递",
                    label: "申通快递"
                },
                {
                    value: "宅急送",
                    label: "宅急送"
                },
                {
                    value: "FedEx联邦",
                    label: "FedEx联邦"
                },
                {
                    value: "韵达快递",
                    label: "韵达快递"
                },
                {
                    value: "百世快递",
                    label: "百世快递"
                },
                {
                    value: "其他",
                    label: "其他"
                }
            ],
            parames: {
                orderId:"",
                expressId: "",
                expressName: "",
                remark: ""
            },
            title:"填写"
        };
    },
    mounted() {
        this.shipmentsDetail();
    },

    methods: {
        async shipmentsDetail() {
            const data = await this.$api.ShipmentsDetial( { orderId: this.$parent.orderId },this.$parent.orderId);
            if(data.expressId==null){
                this.title="填写"
            }else{
                this.title="修改"
            }
            this.parames.orderId=this.$parent.orderId
            this.parames.expressId=data.expressId
            this.parames.expressName=data.expressName
            this.parames.remark=data.remark

        },
        async shipmentsUpdate() {
            const data = await this.$api.ShipmentsUpdate(this.parames,this.$parent.orderId);
            this.$parent.showShipmentsBox = false;
            this.$message({
                message: "成功",
                type: "success"
            });
            this.$parent.getDataList();
        },
        updateShipments() {
            if (this.parames.expressName == "") {
                this.$message.error("请选择物流公司");
                return;
            }
            if (this.parames.expressId == "") {
                this.$message.error("物流单号不能为空");
                return;
            }
            this.shipmentsUpdate()
            
        }
    }
};
</script>
<style scoped>
.shipments-wrap {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
}
.shipments {
    padding: 30px;
    background: #fff;
    border-radius: 10px;
}
.shipments-header {
    font-size: 20px;
    font-weight: 500px;
    border-bottom: 1px solid #eee;
    color: #666;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
}

</style>

