<template>
    <div class="logistics-wrap" @click.self="$parent.showLogisticsBox=false">
        <div class="logistics">
            <div class="logistics-header">
                {{logisticsData.expressName}}-单号:{{logisticsData.expressId}}
                <i
                    class="el-icon-circle-close-outline"
                    style="font-size:26px;color:#888"
                    @click="$parent.showLogisticsBox=false"
                ></i>
            </div>
            <div class="info">
                <div v-if="logisticsData.wuliuLogs.length>0">
                    <p v-for="(item,index) in logisticsData.wuliuLogs" :key="index">
                        {{item}}
                    </p>
                </div>
                <p v-else>暂无物流信息，如有疑问请复制单号使用网页查询</p>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "logistics",
    data() {
        return {
            logisticsData:{}
        };
    },
    mounted() {
        this.logisticsDetail();
    },

    methods: {
        async logisticsDetail() {
            const data = await this.$api.LogisticsDetial( { orderId: this.$parent.orderId,expressId:this.$parent.expressId },this.$parent.orderId);
            this.logisticsData=data
        },
    }
};
</script>
<style scoped>
.logistics-wrap {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
}
.logistics {
    padding: 30px;
    background: #fff;
    border-radius: 10px;
}
.logistics-header {
    font-size: 20px;
    font-weight: 500px;
    border-bottom: 1px solid #eee;
    color: #666;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
}
.info p{
    line-height: 36px;
    color: #999;
    font-size: 16px;
}

</style>

