<template>
    <div class="pay-wrap" @click.self="$parent.showPayBox=false">
        <div class="pay">
            <div class="pay-header">
                退款
                <i
                    class="el-icon-circle-close-outline"
                    style="font-size:26px;color:#888"
                    @click="$parent.showPayBox=false"
                ></i>
            </div>
            <el-form label-width="80px" style="padding:10px 30px 0">
                <el-form-item label="退款金额">
                    <el-input v-model="parames.refundCost" placeholder="请输入退款金额（单位元）"></el-input>
                </el-form-item>
                <el-form-item label="退款原因">
                    <el-input v-model="parames.remark"></el-input>
                </el-form-item>
                <div style="display: flex;justify-content: center;">
                    <el-button size="mini" @click="$parent.showPayBox=false">取消</el-button>
                    <el-button type="primary" size="mini" @click="updatePay">保存</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
export default {
    name: "pay",
    data() {
        return {
            parames: {
                orderId:"",
                returnOrderNo: "",
                refundCost:"",
                // expressName: "",
                remark: ""
            }
        };
    },
    
    methods: {
        async payUpdate() {
            const data = await this.$api.OrderPay(this.parames);
            this.$parent.showPayBox = false;
            this.$parent.getDataList();
        },
        updatePay() {
            this.parames.orderId=this.$parent.orderId
            this.parames.returnOrderNo=this.$parent.returnOrderNo

            if (this.parames.refundCost == "") {
                this.$message.error("退款金额不能为空");
                return;
            }
            this.payUpdate()
            
        }
    }
};
</script>
<style scoped>
.pay-wrap {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
}
.pay {
    padding: 30px;
    background: #fff;
    border-radius: 10px;
}
.pay-header {
    font-size: 20px;
    font-weight: 500px;
    border-bottom: 1px solid #eee;
    color: #666;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 20px;
}

</style>

