<template>
    <div class="address-wrap" @click.self="$parent.showLocationBox=false">
        <div class="address" v-if="parames">
            <div class="address-header">
                修改收货地址
                <i
                    class="el-icon-circle-close-outline"
                    style="font-size:26px;color:#888"
                    @click="$parent.showLocationBox=false"
                ></i>
            </div>
            <el-form ref="form" label-width="80px" style="padding:10px 30px 0">
                <el-form-item label="收货人">
                    <el-input v-model="parames.deliveryName"></el-input>
                </el-form-item>
                <el-form-item label="联系电话">
                    <el-input v-model="parames.deliveryPhone"></el-input>
                </el-form-item>
                <el-form-item label="所在地区">
                    <el-cascader
                        :options="cityOptions"
                        v-model="city"
                        change-on-select
                        style="width:100%"
                    ></el-cascader>
                </el-form-item>
                <el-form-item label="详细地址">
                    <el-input v-model="parames.detailAddress"></el-input>
                </el-form-item>
                <div style="display: flex;justify-content: center;">
                    <el-button size="mini" @click="$parent.showLocationBox=false">取消</el-button>
                    <el-button type="primary" size="mini" @click="updateAddress">保存</el-button>
                </div>
            </el-form>
        </div>
    </div>
</template>
<script>
import cityOptions from "./address.js";
export default {
    name: "address",
    data() {
        return {
            cityOptions: cityOptions,
            city: [],
            parames: {}
        };
    },
    mounted() {
        this.getAddress();
    },

    methods: {
        async getAddress() {
            const data = await this.$api.OrderUserInfo(
                { orderId: this.$parent.orderId },
                this.$parent.orderId
            );
            this.parames = data;
            if (data.provinceName) {
                this.city.push(data.provinceName);
                if (data.cityName) {
                    this.city.push(data.cityName);
                    if (data.countyName) {
                        this.city.push(data.countyName);
                    }
                }
            }
        },
        async editAddress() {
            const data = await this.$api.OrderUserInfoUpdate(
                this.parames,
                this.$parent.orderId
            );
            this.$parent.showLocationBox = false;
            this.$message({
                message: "修改地址成功",
                type: "success"
            });
            this.$parent.getDataList();
        },
        updateAddress() {
            if (this.city[0]) {
                this.parames.provinceName = this.city[0];
                if (this.city[1]) {
                    this.parames.cityName = this.city[1];
                    if (this.city[2]) {
                        this.parames.countyName = this.city[2];
                    }
                }
            }
            if (this.parames.deliveryName == "") {
                this.$message.error("收货人不能为空");
                return;
            }
            if (this.parames.deliveryPhone == "") {
                this.$message.error("联系电话不能为空");
                return;
            }
            if (this.parames.provinceName == "") {
                this.$message.error("所在地区不能为空");
                return;
            }
            if (this.parames.detailAddress == "") {
                this.$message.error("为了准确配送请填写详细地址");
                return;
            }
            this.parames.fullAddress =
                this.city.join("") + this.parames.detailAddress;
            this.editAddress();
        }
    }
};
</script>
<style scoped>
.address-wrap {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 999;
}
.address {
    height: 400px;
    width: 600px;
    background: #fff;
    border-radius: 10px;
}
.address-header {
    padding: 0 30px;
    height: 60px;
    font-size: 20px;
    line-height: 60px;
    font-weight: 500px;
    border-bottom: 1px solid #eee;
    color: #666;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
</style>

